import {
    MenuFoldOutlined,
    MenuUnfoldOutlined,
    UserOutlined,
  } from '@ant-design/icons'
import { Layout, Dropdown, Avatar } from 'antd'
import React, { useState } from 'react'
import './index.less'
import BreadcrumbView from './breadcrumbView'

const { Header } = Layout;



const HeaderView = (props) => {
    const logout = () =>{
        console.log('logout')
    }
    const items = [
        {
            key: '1',
            label: (<span>修改密码</span>),
        },
        {
          key: '2',
          label: (<span onClick={logout}>登出</span>),
        },
    ]

    const { collapsed, currBreadList } = props
    const [user]  = useState({
        name: '系统管理员',
        image: 'https://www.hkqsgl.com/stec-platform-doc/img/img/rBAB-2QdEOyEWFVvAAAAAPHaQeQ084.png?size=80x80'
    })

    return (
        <Header
            className="site-layout-background"
            style={{
                padding: 0,
                height: '65px',
                display: 'flex',
                alignItems: 'center',
            }}
        >
            <div className='header-nav'>
                <div className='left-nav'>
                    <div>
                        {React.createElement(collapsed ? MenuUnfoldOutlined : MenuFoldOutlined, {
                            className: 'trigger',
                            onClick: () => props.collapsedClick(),
                        })}
                    </div>
                    <div>
                        <BreadcrumbView breadList={currBreadList} />
                    </div>
                </div>
                <div className='right-nav'>
                    <span style={{marginRight: '10px'}}>{user.name}</span>
                    <Dropdown
                        menu={{
                            items,
                        }}
                        placement="bottomRight"
                        arrow={{
                            pointAtCenter: true,
                        }}
                        >
                        <Avatar style={{cursor: 'pointer'}} size="large" icon={<UserOutlined />} src={user.image} />
                    </Dropdown>
                </div>
            </div>
        </Header>
    )
}

export default HeaderView