import {Select, Upload, Form, Button,Radio, message} from 'antd'
import { useState } from 'react'
import { publishProject } from '../../api/publish'
import './index.less'

// 发布组件
const Release = (props) => {
    const [fileList, setFileList] = useState([]) // 上传文件列表
    const [uploading, setUploading] = useState(false) // 正在上传状态
    const [valueType, setValueType] = useState(1) // 上传项目类型
    const [valueTypeList, setValueTypeList] = useState([])
    const [projectList, setProjectList] = useState([
        {
            id: 1,
            label: '环莞三期工程建管平台'
        },
        {
            id: 2,
            label: '城盾隧安信息化管控平台'
        }
    ]) // 项目列表
    const [projectId, setProjectId] = useState(0)
    const dictMap = {
        1: [
            { key: 1, name: 'pc', label: 'PC'},
        ],
        2: [
            { key: 1, name: 'pc', label: 'PC'},
            { key: 2, name: 'app', label: 'APP'}
        ]
    }
    const [form] = Form.useForm()
  
    // 上传文件
    const handleUploadFile = (fileList,data) => {
        const formData = new FormData()
        formData.append('file',fileList[0].originFileObj)
        formData.append('projectId',data.projectId)
        const type = dictMap[data.projectId].find(item => valueType === item.key).name
        formData.append('type', type)
        setUploading(true)
        publishProject(formData).then(res=>{
            if (res.data.success) {
                message.success('发布成功')
            } else {
                message.error('发布失败:'+res.data.message)
            }
            form.setFieldValue('projectId', '')
            setFileList([])
        }).catch(err=>{
            console.error('服务器异常', err)
            message.error('服务器异常:'+err)
        }).finally(()=>{
            setUploading(false)
        })
    }
  
    // 表单提交操作
    const onFinish = (data) => {
        if (!fileList.length) {
            message.error('请先上传文件')
            return
        }
        handleUploadFile(fileList, {projectId: data.projectId})
    }
  
    // 上传之前
    const beforeUpload = (file) =>{
        setFileList([...fileList, file]);
        return false;
    }
  
    //  移除文件
    const onRemove = (file) => {
        const index = fileList.indexOf(file);
        const newFileList = fileList.slice();
        newFileList.splice(index, 1);
        setFileList([...newFileList])
    }
  
    // 文件有变化时
    const onChange = (file) =>{
        setFileList(file.fileList)
    }
  
    // 选择项目后的回调
    const handleProjectChange = (value) => {
        // console.log(`selected ${value}`, dictMap[value]);
        setValueTypeList(dictMap[value]?dictMap[value]:dictMap[1])
        setValueType(1)
        setProjectId(value)
    }
  
    // 项目类型变化
    const onTypeChange = (e) => {
        // console.log('onTypeChange', e.target.value)
        setValueType(e.target.value)
    }

    return (
        <div className="container">
          <div className='header-div'>
              <h2>项目发布系统</h2>
              <span>本项目针对无法自动发布的项目而开发,步骤一:选择需要发布的项目, 步骤二:上传项目文件,步骤三:点击发布</span>
          </div>
          <div className="main-div">
              <Form
                  labelCol={{
                      span: 4,
                  }}
                  wrapperCol={{
                      span: 8,
                  }}
                  form={form}
                  layout="horizontal"
                  onFinish={onFinish}
              >
                  <Form.Item label="项目名称" 
                      name="projectId"
                      placeholder="sdd"
                      rules={[
                          {
                              required: true,
                              message: '请选择项目',
                          },
                      ]}>
                      <Select allowClear placeholder="请选择项目" onChange={handleProjectChange}  value={projectId}>
                        {
                            projectList.map((project, index) => {
                                return  <Select.Option value={project.id} key={index}>{project.label}</Select.Option>
                            })
                        }
                      </Select>
                  </Form.Item>
    
                  <Form.Item label="项目类型">
                      <Radio.Group onChange={onTypeChange} value={valueType}>
                          {
                              valueTypeList.map((valueObj, index)=>{
                                  return <Radio value={valueObj.key} key={index}>{valueObj.label}</Radio>
                              })
                          }
                      </Radio.Group>
                  </Form.Item>
    
                  <Form.Item label="项目文件" 
                      valuePropName="fileList"
                      >
                      <Upload
                          fileList={fileList}
                          beforeUpload={beforeUpload} 
                          onRemove={onRemove}
                          onChange={onChange}
                          maxCount={1}>
                          <Button>点击选择</Button>
                      </Upload>
                  </Form.Item>
                  <Form.Item label="发布">
                      <Button
                          disabled={fileList.length === 0}
                          type="primary" 
                          htmlType="submit" 
                          loading={uploading}>
                          {uploading ? '正在上传' : '提交'}
                      </Button>
                  </Form.Item>
              </Form>
          </div>
        </div>
      )
}

export default Release